/* Фикс бага видимости освновного меню: после открытия-закрытия sendvich-menu на адаптивном размере экрана,
и перехода на неадаптивный размер экрана, основное меню было скрыто, т.к.
jQuery меняла видимость освновного меню в html. */
@media (min-width: 701px) {
